.resume {
  padding-top: 20px;
  margin: 0 10%; }
  .resume h4, .resume .location {
    margin: 0 5%;
    padding-top: 1.5rem; }
  .resume .location {
    font-style: italic; }
  .resume .tag {
    background-color: #a0c4ff;
    padding: .3em;
    margin: 0 5%;
    text-transform: uppercase; }
  .resume .tag + h4 {
    padding-top: .5rem; }
  .resume p {
    margin-top: .5rem; }
    .resume p a:before {
      text-decoration: none;
      content: ' '; }
    .resume p a:after {
      content: ' ';
      text-decoration: none; }

.resume-section-content {
  display: inline-block;
  width: 100%; }



